.catalog-hero {
  padding-bottom: 140px;
  &__content {
    position: relative;
    border-radius: 20px;
    background: #f8f8f8;
    overflow: hidden;
    margin-bottom: 10px;
    height: 165px;
    padding: 23px;
    &:after {
      content: "";
      position: absolute;
      bottom: 0;
      right: 60px;
      background: url("../../img/catalog-hero-img.webp") center / cover
        no-repeat;
      width: 435px;
      height: 165px;
    }
  }

  .hero__title {
    text-align: left;
    color: var(--gray);
    margin: 0;
  }

  &__futures {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 20px;
    margin-bottom: 65px;
  }

  &__future {
    border-radius: 20px;
    display: flex;
    align-items: center;
    gap: 20px;
    background: #f8f8f8;
    padding: 25px 32px;
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 18px;
    line-height: 97%;
    letter-spacing: -0.01em;
    color: var(--siniy);
    .icon-future {
      width: 68px;
      height: 68px;
      display: grid;
      place-items: center;
      border-radius: 5px;
      background: var(--white);
    }
  }
}

.catalog-offer {
  margin-bottom: 100px;
  &__heading {
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
    gap: 20px;
    margin-bottom: 40px;
  }

  &__title {
    font-family: var(--font-family);
    font-weight: 700;
    font-size: clamp(22px, 3vw, 30px);
    line-height: 97%;
    letter-spacing: -0.01em;
    color: var(--gray);
  }

  &__counter {
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 14px;
    line-height: 97%;
    letter-spacing: -0.01em;
    color: #272727;

    @media (max-width: 767px) {
      display: none;
    }
  }

  &__tags {
    display: flex;
    flex-wrap: wrap;
    max-width: 745px;
    column-gap: 15px;
    row-gap: 24px;
    margin-bottom: 40px;
    .tag a {
      border-radius: 100px;
      padding: 10px 22px;
      background: #f8f8f8;
      font-family: var(--font-family);
      font-weight: 500;
      font-size: 14px;
      line-height: 97%;
      letter-spacing: -0.01em;
      color: var(--tekst);
      transition: var(--transition);

      @media (any-hover: hover) {
        &:hover {
          background: var(--accent);
          color: var(--white);
        }
      }

      &.active {
        background: var(--accent);
        color: var(--white);
      }
    }
  }

  &__items {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 20px;
  }

  &__footer {
    margin-top: 45px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;

    .pagination {
      display: flex;
      gap: 13px;

      a {
        border-radius: 5px;
        background: var(--blue);
        width: 40px;
        height: 40px;
        display: grid;
        place-items: center;
        font-family: var(--font-family);
        font-weight: 700;
        font-size: 16px;
        line-height: 97%;
        letter-spacing: -0.01em;
        color: var(--accent);
        transition: var(--transition);

        @media (any-hover: hover) {
          &:hover {
            background: var(--accent);
            color: var(--white);
          }
        }

        &.active {
          background: var(--accent);
          color: var(--white);
        }
      }
    }
  }

  &__more {
    background: var(--accent);
    color: var(--white);
    padding-block: 17px;
    border-radius: 10px;
    max-width: 240px;
    width: 100%;

    @media (any-hover: hover) {
      &:hover {
        background: var(--hovered);
      }
    }
  }
}

.catalog-seo {
  margin-bottom: 100px;
  .container {
    border-radius: 20px;
    background: var(--blue);
    padding: 32px 45px;
  }

  &__wrapper {
    background: var(--white);
    padding: 40px;
    border-radius: 20px;

    h2 {
      font-family: var(--font-family);
      font-weight: 700;
      font-size: clamp(20px, 3vw, 30px);
      line-height: 97%;
      letter-spacing: -0.01em;
      color: var(--gray);
    }

    p {
      font-family: var(--font-family);
      font-weight: 500;
      font-size: 16px;
      line-height: 136%;
      letter-spacing: -0.02em;
      color: var(--gray);
    }

    li {
      font-family: var(--font-family);
      font-weight: 500;
      font-size: 16px;
      line-height: 136%;
      letter-spacing: -0.02em;
      color: var(--gray);
    }
  }
}
